html {
  scroll-behavior: smooth; }

body {
  margin: 0;
  font-family: 'Roboto', sans-serif; }

.wrapper {
  padding: 0 20px; }

.wrapper-max {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 20px; }

.display-flex {
  display: flex; }

.space-between {
  justify-content: space-between; }

.flex-1 {
  flex: 1 1; }

.flex-2 {
  flex: 2 1; }

.break {
  margin: 45px 30px; }

.relative {
  position: relative; }

select {
  border: 0;
  background-color: transparent;
  padding: 5px;
  height: 30px;
  width: 50px;
  font-size: 18px; }

button {
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
  border-radius: 0; }
  @media (min-width: 900px) {
    button:hover {
      background-color: grey !important; } }

input:focus, select:focus, button:focus {
  outline: 0; }

header.nav-header {
  background-color: lightgrey; }
  header.nav-header h1 {
    margin: 0 auto;
    padding: 6px 20px; }
  header.nav-header .nav-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 15px 30px; }
    header.nav-header .nav-wrapper .main-nav {
      padding: 0;
      margin: 0;
      list-style: none;
      display: flex; }
      header.nav-header .nav-wrapper .main-nav li {
        padding: 0 10px; }

.score-book {
  width: 100%; }
  .score-book header.position {
    display: flex;
    width: 100%; }
    .score-book header.position .player-info {
      flex: 2 1;
      text-align: center; }
    .score-book header.position .inning {
      flex: 1 1;
      text-align: center; }
  .score-book div.position {
    display: flex;
    width: 100%; }
    .score-book div.position .player-info {
      border: 1px solid grey;
      flex: 2 1;
      text-align: center;
      display: flex; }
      .score-book div.position .player-info .batting-order {
        width: 30px; }
      .score-book div.position .player-info .player-name .player-number {
        font-size: 0.85em;
        font-weight: 800; }
    .score-book div.position .inning {
      border: 1px solid grey;
      flex: 1 1;
      text-align: center; }

.game-profile .two-column {
  height: calc(100vh - 95px); }
  .game-profile .two-column .col {
    flex: 1 1; }
    .game-profile .two-column .col.left {
      flex: 2 1; }

.view-toggle {
  padding-top: 20px; }
  .view-toggle button {
    padding: 5px 10px;
    margin: 0;
    border: none;
    border-left: 1px solid lightgrey;
    background-color: #e6e6e6; }
    .view-toggle button:first-child {
      border-left: none; }
    .view-toggle button.active {
      background-color: grey;
      color: white; }

.coach-profile .two-column {
  height: calc(100vh - 50px);
  overflow: hidden; }
  @media (max-width: 900px) {
    .coach-profile .two-column {
      flex-direction: column;
      padding: 0;
      overflow: visible;
      overflow: initial;
      height: auto;
      height: initial; } }
  .coach-profile .two-column .col {
    flex: 1 1;
    padding: 0 20px; }
    .coach-profile .two-column .col.left {
      padding-top: 30px; }
      @media (max-width: 900px) {
        .coach-profile .two-column .col.left {
          background-image: -webkit-gradient(linear, left top, left bottom, from(#ffffff), color-stop(#ffffff), to(#f2f2f2));
          background-image: linear-gradient(#ffffff, #ffffff, #f2f2f2); } }
      @media (max-width: 765px) {
        .coach-profile .two-column .col.left {
          background-image: -webkit-gradient(linear, left top, left bottom, from(#ffffff), color-stop(#ffffff), color-stop(#f2f2f2), color-stop(#f2f2f2), to(#f2f2f2));
          background-image: linear-gradient(#ffffff, #ffffff, #f2f2f2, #f2f2f2, #f2f2f2); } }
      @media (max-width: 900px) {
        .coach-profile .two-column .col.left .left-wrapper {
          display: flex;
          padding: 30px 0; }
          .coach-profile .two-column .col.left .left-wrapper > div,
          .coach-profile .two-column .col.left .left-wrapper > article {
            flex: 1 1; } }
      @media (max-width: 765px) {
        .coach-profile .two-column .col.left .left-wrapper {
          display: block; } }

.coach-profile header.player-list-header {
  box-shadow: 0px 4px 6px -6px grey;
  border-bottom: 1px solid lightgrey;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  position: relative; }
  @media (max-width: 900px) {
    .coach-profile header.player-list-header {
      position: -webkit-sticky;
      position: sticky;
      top: 0px;
      background-color: #ffffff;
      z-index: 2;
      height: 67px; } }
  .coach-profile header.player-list-header .header-wrapper h1 {
    margin: 10px 0 0;
    font-size: 2em; }
  .coach-profile header.player-list-header button.add-player {
    position: absolute;
    padding: 5px 10px;
    margin: 0;
    border: none;
    background-color: darkgreen;
    color: #ffffff;
    bottom: 0;
    right: 0; }

.player {
  padding: 20px 0;
  border-bottom: 1px solid lightgray; }
  .player header {
    background-color: transparent;
    margin: 0 0 20px 0;
    position: relative; }
    .player header h2.player-name {
      margin: 0; }
      .player header h2.player-name .jersey-number {
        font-size: 0.8em; }
    .player header div.player-details {
      margin: 0; }
    .player header button {
      position: absolute;
      top: 0;
      right: 0;
      margin-right: 10px;
      border: none;
