  overflow: scroll; }
  @media (max-width: 900px) {
    .players-list {
      overflow: visible;
      height: auto;
      height: initial; } }

.player-stats header {
  padding: 5px 0;
  padding-top: 20px;
  margin: 0; }
  .player-stats header h2 {
    padding: 0;
    margin: 0; }
    .player-stats header h2 .jersey-number {
      font-size: 0.8em; }
  .player-stats header .player-details {
    font-size: 16px;
    margin-bottom: 5px; }

.player-stats .row {
