form.add-player {
  width: 100%;
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
  padding: 5px 0 15px;
  box-shadow: 0px 4px 6px -6px grey;
  border-bottom: 1px solid lightgrey;
  z-index: 1;
  background-color: #ffffff;
  position: absolute; }
  @media (max-width: 900px) {
    form.add-player {
      position: -webkit-sticky;
      position: sticky;
      top: 107px; } }
  form.add-player input[type="text"], form.add-player input[type="number"] {
    width: calc(50% - 20px);
    border: none;
    border-bottom: 1px solid grey;
